header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0072C6;
  color: #fff;
  font-weight: bold;
  text-transform: capitalize;
  padding: 10px 25px;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}
header h1 {
  font-size: 1.5rem;
}
header .adduser {
  color: #566787;
  background-color: #fff;
  font-weight: 400;
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 5px 15px;
  white-space: nowrap;
}
header .adduser:hover {
  box-shadow: 1px 1px 3px #0f164b;
  transition: box-shadow 0.3s ease-in-out;
  font-weight: 600;
}/*# sourceMappingURL=Header.css.map */